<template>
<!-- 承包人员信息 -->
  <div class="personnel-info">
    <div class="title">
      <navBar :titleText="'承包人员信息'"></navBar>
    </div>
    <div class="info-body">
      <van-cell class="info-name" center :title="info.name">
         <template #label>
            {{info.sex}} · {{info.age}} · {{info.education}}
          </template>
      </van-cell>
      <van-cell class="info-name" center title="身份证号">
         <template #label>
            {{info.passportId ? info.passportId : '无'}}
          </template>
      </van-cell>
      <van-cell class="info-name" center title="职称">
         <template #label>
            {{info.title ? info.title : '无'}}
          </template>
      </van-cell>
      <van-cell class="info-name" center title="职务">
         <template #label>
            {{info.jobName ? info.jobName : '无'}}
          </template>
      </van-cell>
      <van-cell class="info-name" center title="单位属性">
         <template #label>
            {{info.unitName ? info.unitName : '无'}}
          </template>
      </van-cell>
      <van-cell class="info-name" center title="工种">
         <template #label>
            {{info.personWorkType ? info.personWorkType : '无'}}
          </template>
      </van-cell>
      <van-cell class="info-name" center title="是否农民工">
         <template #label>
            {{info.isMigrantWorkers ? info.isMigrantWorkers : '无'}}
          </template>
      </van-cell>
      <van-cell class="info-name" center title="作业类型">
         <template #label>
            {{info.workType ? info.workType : '无'}}
          </template>
      </van-cell>
      <van-cell class="info-name" center title="工作履历">
         <template #label>
            {{info.resume ? info.resume : '无'}}
          </template>
      </van-cell>
      <van-cell class="info-name" center title="备注">
         <template #label>
            {{info.remark ? info.remark : '无'}}
          </template>
      </van-cell>
      <template>
        <div class="view-image-box" v-for="(item, i) in imgBoxData" :key="i">
          <p :style="item.fileList.length == 0 ? 'color: #CCCCCC' : ''">{{item.title}}</p>
          <van-uploader v-model="item.fileList" :deletable="false" :show-upload='false' multiple disabled/>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import navBar from '@/components/navBar.vue'
export default {
  name: 'PersonnelInfo',
  components: {
    navBar
  },
  created () {
    console.log(this.$route.query)
  },
  data () {
    return {
      info: {},
      imgBoxData: [
        {
          title: '资质证书',
          fileList: [
          ]
        },
        {
          title: '安全生产责任险',
          fileList: [
          ]
        },
        {
          title: '安全培训',
          fileList: []
        },
        {
          title: '社会保险证明',
          fileList: [
          ]
        },
        {
          title: '健康体检证明',
          fileList: [
          ]
        }
      ]
    }
  },
  methods: {
    init () {
      this.getPersonnelInfo()
    },
    getPersonnelInfo () {
      this.info = this.$route.query.message.data.data
      if (this.info.certifyDoc && this.info.certifyDoc.length > 0) {
        const array = []
        for (let i = 0; i < this.info.certifyDoc.length; i++) {
          array.push({
            url: `${this.$baseUrl}/CMS/upload${this.info.certifyDoc[i].filePath}`
          })
        }
        this.imgBoxData[0].fileList = array
      }
      if (this.info.safeProduct && this.info.safeProduct.length > 0) {
        const array = []
        for (let i = 0; i < this.info.safeProduct.length; i++) {
          array.push({
            url: `${this.$baseUrl}/CMS/upload${this.info.safeProduct[i].filePath}`
          })
        }
        this.imgBoxData[1].fileList = array
      }
      if (this.info.safeTrain && this.info.safeTrain.length > 0) {
        const array = []
        for (let i = 0; i < this.info.safeTrain.length; i++) {
          array.push({
            url: `${this.$baseUrl}/CMS/upload${this.info.safeTrain[i].filePath}`
          })
        }
        this.imgBoxData[2].fileList = array
      }
      if (this.info.issueProve && this.info.issueProve.length > 0) {
        const array = []
        for (let i = 0; i < this.info.issueProve.length; i++) {
          array.push({
            url: `${this.$baseUrl}/CMS/upload${this.info.issueProve[i].filePath}`
          })
        }
        this.imgBoxData[3].fileList = array
      }
      if (this.info.healthyProve && this.info.healthyProve.length > 0) {
        const array = []
        for (let i = 0; i < this.info.healthyProve.length; i++) {
          array.push({
            url: `${this.$baseUrl}/CMS/upload${this.info.healthyProve[i].filePath}`
          })
        }
        this.imgBoxData[4].fileList = array
      }
    }
  },
  mounted () {
    this.init();
  }
}
</script>

<style lang='less' scoped>
    * {
      padding: 0;
      margin: 0;
    }
  .personnel-info {
    box-sizing: border-box;
    padding: 0rem 1rem;
    .info-body {
      .van-cell {
        margin: 1rem 0rem;
      }
      .info-name {
        .van-cell__title {
          font-size: 1.5rem;
          font-weight: 500;
        }
      }
      .van-cell__title {
        font-size: 1rem;
        .van-cell__label {
          font-size: 0.875rem;
          margin-top: 0.25rem;
        }
      }
      .view-image-box {
        box-sizing: border-box;
        padding: 0rem 1rem;
        margin-bottom: 1rem;
        border: 1px solid #B4B4B4;
        border-radius: 0.75rem;
        font-size: 1rem;
        p {
          margin: .5rem 0rem;
        }
      }
    }
  }
</style>
